@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;400;500;800;900&display=swap');*/
:root {
  --main-color: #323232;
  --text-color: #D4D4D4;
  --sub-color: #FFAC41;
}
*
{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body
{
  background-color: #323232;
  background-color: var(--main-color);
  font-family: 'Roboto', sans-serif;
}

.con
{
  width: 100%;
  padding:10vw 20vh;
}
/*Navbar styles*/
.navbar
{
  font-weight: 100;
  color: #D4D4D4;
  color: var(--text-color);
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 1em 10em;
}
.logo
{
  font-size: 4vw;
  color: #FFAC41;
  color: var(--sub-color);
}
.nav-links
{
  display: flex;
  list-style: none;
  justify-content: space-around;
  font-size: 1.45vw;
  width: 45%;
  align-items: center;
}

/*Hero Section*/
.hero-sec
{
  display: flex;
  position: relative;
  color: #D4D4D4;
  color: var(--text-color);
  margin: 10% 9%;
  justify-content: space-between;
  height: 50vh;
}
.hero-sec .heroaf {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: 45vh;
  background-color: #FFAC41;
  background-color: var(--sub-color);
  top: 0;
  left: -18px;
}
.hero-titles-sec
{
  display: flex;
  flex-direction: column;
  font-size: 1.7em;

}
.hero-title
{
  font-weight: 600;
  font-size: 5vw;

}
h3.hero-sub-title {
  padding: 0.5em 0;
  font-size: 1.6em;
  font-weight: 300;
}

.tech-langs
{
  letter-spacing: 13px;
}
.name
{
  color: #FFAC41;
  color: var(--sub-color);
}
#OA-logo{
  position: absolute;
  right: 7%;
  z-index: -1;
  bottom: 1%;
  overflow: hidden;
}
/*hero image*/
.hero-img
{
    width: 400px;
    height: 400px;
    border-radius: 50%;
}
/*About section*/
.about
{
  width: 100%;
  height: 100vh;
  background-color: #FFAC41;
  background-color: var(--sub-color);
  overflow: hidden;
}
.about-title
{
  font-weight: 100;
  font-size: 5em;
}
.about-p {
  position: relative;
  font-size: 2vw;
  font-weight: 400;
  letter-spacing: 7px;
  line-height: 1.3em;
  position: relative;
  z-index: 1;
}
.about-p::after
{
  position: absolute;
  content: " ";
  width: 300px;
  height: 300px;
  background-color: #fff;
  border-radius: 50%;
  z-index: -1;
  top: -13px;
  left: -94px;
}
.cus
{
  width: 100%;
  height: 50vh;
  background-image: url(https://images.pexels.com/photos/450035/pexels-photo-450035.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cus-op
{
  z-index: 2;
}
.op
{
  text-align: center;
  color: #fff;
  opacity: 1;
}
.op h3
{
  padding: 10px 0;
  font-size: 4em;
}
.op p
{
  font-size: 1.5em;

}
.cus::after
{
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: black;
  z-index: 0;
  opacity: 0.5;
}
.project:hover .cus::after
{
  opacity: 0.8;
}
/*projects section*/
.projects-section
{
  display: flex;
  flex-direction: column;
  background-color: #fff;
}
.projects-section h1
{
  text-align: center;
}
.projects-sec
{
  display: grid;
  height: auto;
  grid-template-columns: 50% 50%;
  background-color: #fff;
  justify-items: center;
}
.project
{
  position: relative;
}
.project-img
{
  position: relative;
  width: 100%;
  height: 100%;
}
.project::after
{
  width: 100%;
  content: "";
  height: 100%;
  position: absolute;
  background-color: black;
  z-index: 0;
  top: 0;
  left: 0;
  opacity: 0.5;
}
.project-txt-con
{
  position: absolute;
  bottom: 0;
  color: #fff;
  z-index: 1;
  padding: 1em;
}
.container
{
  width: 650px;
max-width: 100%;
margin: 20px auto 25px auto;
}
.project:hover .overlay
{
  display: block;
  opacity: 0.6;
  transition: 0.2s;
}
.overlay
{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  top: 0;
  opacity: 0;
  z-index: 0;
  display: none;
}
input, textarea
{
  margin: 1rem;
  padding: 30px;
border: 0;
width: 100%;
font-size: 1rem;
background-color: #2d2d2d;
color: white;
border-radius: 4px;
}
.head-con
{
  color: #fff;
  font-size: 30px;
  font-weight: 900;
  letter-spacing: 3px;
  line-height: 1.3em;
  text-align: center;
  padding: 30px;
}
.submit {
  padding: 16px 20px;
  background: none;
  color: #fff;
  border: 0.5px solid;
  margin: 1rem;
  border-radius: 4px;
  cursor: pointer;
}
.footer
{
  text-align: center;
  padding: 25px;
  background-color: #2d2d2d;
  color: #fff;
  font-weight: 900;
}
.previewBtn
{
  display: none;
  opacity: 0;
  color: #fff;
  text-decoration: none;
  position: absolute;
  z-index: 3;
  left: 40%;
  top: 45%;
  padding: 16px 20px;
  border: 1px #fff solid;
  border-radius: 5px;
  transition: background 0.6s;
}
.project-txt-con *
{
  display: none;
  opacity: 1;
}
.previewBtn:hover
{
  background-color: #fff;
  color: #000;
}
@media (max-width: 900px) {
  .logo
  {
    font-size: 20vw;
  }
  .about-title
  {
    font-size: 4em;
  }
  .nav-links {
    display: none;
  }
.contact {
    width: 85%;
    margin: auto;
}
  .hero-sec
  {
    height: 80vh;
  }
  .hero-title
  {
    font-size: 1em;
  }
  .hero-titles-sec
  {
    font-size: 2.5em;
  }
  .tech-langs {
    letter-spacing: 0;
    font-size: 0.5em;
}
.hero-sec .heroaf {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: 22vh;
  background-color: #FFAC41;
  background-color: var(--sub-color);
  top: 0;
  left: -18px;
}
.con
{
  padding: 0;
}
.about-p
{
  font-size: 30px;
  padding: 75px 10px;
}
.projects-sec
{
  grid-template-columns: auto;
}
.project {
  position: relative;
  width: 90%;
}
.op p
{
  font-size: 1.2em;
}
.op h3
{
  font-size: 3em;
}
input, textarea
{
  margin: 10px 0;
}
}
